import opg from 'ts/opg.ts';
import Panel from 'ts/ui/Panel.ts';
import Table from 'ts/ui/Table.ts';
import {Combo} from 'ts/ui/Combo';
import {Languages} from 'ts/util/Languages';
import {store} from '../../../ts/util/store';
import PopUp from "../../../ts/ui/Popup";
import {url} from "../../../ts/util/utils";
import {request, string, dateTime, is, url, convert, format, array} from  'ts/util/utils';



opg.api({
	'getboardlistbyuser!post': 'Operation/GetBoardlistbyuser',
    'addboard!post':'Operation/AddBoard',
    'getBoothByBarcode!post': 'Operation/GetBoothByBarcode',
});

const lpg = Languages.package;

let clng='en';
let barcode='';

if(localStorage.getItem("lngPkg")!=null|| localStorage.getItem("lngPkg")!=undefined)
{
    const currentlng=localStorage.getItem("lngPkg");
    clng=currentlng.replace(/["""]/g,"");
}


opg.api.getBoothByBarcode({barcode:barcode},data=>{
    if(data.length>0) {
        $("#standno").val(data[0].booth_no);
        $("#companycn").val(data[0].company_cn);
        $("#company").val(data[0].company);
    }
});





function refreshtable() {
    let xparam = {}
    xparam.pageIndex = 0;
    xparam.currentlng=clng;
    tb.update(xparam);
}


let tbButtons = [];

let tb: Table = opg('#tb').table({
	api: opg.api.getboardlistbyuser,
	lazy: true,
	titleBar: {
		title: `${lpg.list}`,
		buttons: tbButtons,
	},
	columns: [
		{
			text: `${lpg.booth}`, width: 50,
			src: 'standno',
		},
		{
			text: `${lpg.companycn}`, width: 100,
			src: 'company_cn',
		},
        {
            text: `${lpg.company}`, width: 100,
            src: 'company',
        },
        {
            text: `${lpg.createtime}`, width: 100,
            src: 'createtime',
        },
        {
            text: `${lpg.status}`, width: 50,
            src: 'status',
            render:function (id, index, row) {
                let html = ``;
                if (row.status==='0') {
                    html += `<label class="apply">${lpg.statustype[0]}</label>`;
                }
                else if(row.status==='1') {
                    html += `<label class="agree">${lpg.statustype[1]}</label>`;
                }
                else if(row.status==='2') {
                    html += `<label class="deny">${lpg.statustype[2]}</label>`;
                }
                return html;
            }
        }
	],
	pagination: true,
});

$("#btnReset").click(function() {
    location.reload(true);
});

$("#btnSaveMain").click(function() {
    let param=$("#tbMain").fieldsToJson();

    opg.api.addboard(param, function (data) {
        location.reload(true);
    });
});

refreshtable();

function formatdate(mydate) {
    var newdate;
    var data = new Date(mydate);
    var year = data.getFullYear();  //获取年
    var month = data.getMonth() + 1;    //获取月
    var day = data.getDate(); //获取日
    if(month<10) {
        if(day<10)
            newdate= year+"-0"+month+"-0"+day;
        else
            newdate= year+"-0"+month+"-"+day;
    }
    else {
        if(day<10)
            newdate = year + "-" + month+"-0"+day;
        else
            newdate = year + "-" + month + "-" + day;
    }
    return newdate;
}

